import uuid from 'uuid/v4';

const SET_SCREEN = 'SET_SCREEN';

const SCREEN_VAULT = 'SCREEN_VAULT';
const SCREEN_MAX = 'SCREEN_MAX';
const SCREEN_ACCOUNT_DETAILS = 'SCREEN_ACCOUNT_DETAILS';
const RELOAD_ACCOUNT = 'RELOAD_ACCOUNT';

export {
  SET_SCREEN,
  SCREEN_VAULT,
  SCREEN_MAX,
  SCREEN_ACCOUNT_DETAILS,
  RELOAD_ACCOUNT,
};

export const setScreen = screen => ({
  type: SET_SCREEN,
  payload: {
    screen,
  },
});

export const reloadAccount = () => ({
  type: RELOAD_ACCOUNT,
  payload: {
    reloadAccountNonce: uuid(),
  },
});
