import Lockr from 'lockr';
import qs from 'qs';

import { isValidCode } from './tronCommon';
import { SCREEN_VAULT } from '../actions/app';

const REFERRAL_ADDRESS_KEY = 'TRON_MINE_REFERRAL'; 

export const parseReferralAddress = () => {
  const storedReferralAddress = Lockr.get(REFERRAL_ADDRESS_KEY);
  const parsedQueryString = qs.parse(window.location.search.substr(1));
  let referralAddress = parsedQueryString['ref'];
  if (referralAddress && isValidCode(referralAddress)) {
    console.log(referralAddress);
    // store the new referralAddress
    Lockr.set(REFERRAL_ADDRESS_KEY, referralAddress);
  } else if (!!storedReferralAddress) {
    referralAddress = storedReferralAddress;
  } else {
    referralAddress = 0; // FOUNDATION_CODE; default is 0, so the first user without a code won't use admin as referrer
  }
  return referralAddress;
};

export const parseGameRoute = () => {
  return SCREEN_VAULT;
};
