import $ from 'jquery';

$(function() {
  function walk() {
    var left = $('img.walk').offset();
    $('img.walk').css('left', left);
    $('img.walk').animate(
      {
        left: '+=' + walkDistance(),
      },
      7000,
      () => {
        // Animation complete.
        $('img.walk').addClass('flip');
        $('img.walk').animate(
          {
            left: '+=0',
          },
          2000,
          () => {
            $('img.walk').animate(
              {
                left: '-=' + walkDistance(),
              },
              7000,
              () => {
                $('img.walk').removeClass('flip');
                walk();
              }
            );
          }
        );
      }
    );
  }

  function walkDistance() {
    var width = $(window).width();
    if (width <= 480) {
      return '50%';
    } else if (width <= 768) {
      return '45%';
    } else if (width <= 1200) {
      return '35%';
    }
    return '30%';
  }

  function resize() {
    var height = $('.mine').height();
    if (height < 873) {
      var ratio = height / 873;
      $('.walk').width(130 * ratio);
      $('.walk').height(121 * ratio);
      $('.walk').show(() => {
        walk();
      });
    }
  }

  resize();
});
