import TronWeb from 'tronweb';
import BigNumber from 'bignumber.js';
import store from '../store';
import { showSnackbar } from '../actions/snackbar';
import { reloadAccount } from '../actions/app';
import {
  SNACKBAR_ERROR,
  SNACKBAR_INFO,
} from '../components/Common/GameSnackbar';

import {
  FOUNDATION_ADDRESS,
  initContract,
  injectTronWeb,
  sunToTrx,
  TRON_WEB_STATE_LOCKED,
  TRON_WEB_STATE_NOT_INSTALLED,
  TRON_WEB_STATE_NOT_MAINNET,
  trxToSun,
  getDateTimeLongFromUnix,
} from './tronCommon';

import { toTruncateDown } from './math';

const gasLimit = 50 * 1000000; // 50 trx

// MAINNET GO LIVE
const CONTRACT_ADDRESS = '410a7e3d6ee8eee0c347b948459d45fc42a234a1ea';
const TRONGRID_API = 'https://api.trongrid.io';

const initDefaultTronWeb = () => {
  const defaultTronWeb = new TronWeb(TRONGRID_API, TRONGRID_API, TRONGRID_API);

  // Set default address (foundation address) used for contract calls
  // Directly overwrites the address object as TronLink disabled the
  // function call
  defaultTronWeb.defaultAddress = {
    hex: defaultTronWeb.address.toHex(FOUNDATION_ADDRESS),
    base58: FOUNDATION_ADDRESS,
  };

  return defaultTronWeb;
};

export const defaultTronWeb = initDefaultTronWeb();

let currentAddress;
let contract;
let tronWebState;
const monitorAddressChanged = () => {
  window.tronWeb.on('addressChanged', async address => {
    if (currentAddress !== address.base58) {
      const needsToNotify = !!currentAddress;
      currentAddress = address.base58;

      tronWebState = await injectTronWeb(defaultTronWeb);
      contract = await initContract(
        tronWebState,
        defaultTronWeb,
        CONTRACT_ADDRESS
      );
      if (needsToNotify) {
        store.dispatch(showSnackbar('message.addressChanged', SNACKBAR_INFO));
        store.dispatch(reloadAccount());
      }
    }
  });
};

export default {
  async initTronWeb() {
    tronWebState = await injectTronWeb(defaultTronWeb);
    contract = await initContract(
      tronWebState,
      defaultTronWeb,
      CONTRACT_ADDRESS
    );
    if (!contract) {
      tronWebState = TRON_WEB_STATE_NOT_MAINNET;
      store.dispatch(
        showSnackbar('message.needToChangeNetwork', SNACKBAR_ERROR)
      );
    }

    monitorAddressChanged();

    if (tronWebState === TRON_WEB_STATE_NOT_INSTALLED) {
      store.dispatch(showSnackbar('message.requireInstall', SNACKBAR_INFO));
    } else if (tronWebState === TRON_WEB_STATE_LOCKED) {
      store.dispatch(showSnackbar('message.requireLogin', SNACKBAR_INFO));
    }
    return tronWebState;
  },

  getCurrentAddress() {
    return window.tronWeb.defaultAddress.base58;
  },

  // Main functions
  async currentAddressBalance() {
    if (tronWebState === TRON_WEB_STATE_LOCKED) {
      return 0;
    }
    const balance = await window.tronWeb.trx.getBalance();
    return balance;
  },

  async getBalance() {
    const balance = await contract.getBalance().call();
    return sunToTrx(new BigNumber(balance).toNumber());
  },

  async getPlayerCount() {
    const playerCode = await contract.getLatestReferrerCode().call();

    const latestCode = playerCode ? new BigNumber(playerCode).toNumber() : 0;
    let playerCount = 0;
    if (latestCode > 1000) {
      playerCount = latestCode - 1000;
    }

    return playerCount;
  },

  async getDivsAllData() {
    const currentAddress = this.getCurrentAddress();
    if (!currentAddress || currentAddress === FOUNDATION_ADDRESS) {
      return {
        withdrawable: 0,
        investment: 0,
        referral: 0,
      };
    }

    const data = {};
    try {
      const playerUID = await contract.getUIDByAddress(currentAddress).call();
      const playerInfo = await contract.getInvestorInfoByUID(playerUID).call();

      data.playerCode = new BigNumber(playerUID).toNumber();
      data.referrerEarnings = playerInfo.referrerEarnings
        ? sunToTrx(new BigNumber(playerInfo.referrerEarnings).toNumber())
        : 0;
      data.availableReferrerEarnings = playerInfo.availableReferrerEarnings
        ? sunToTrx(
            new BigNumber(playerInfo.availableReferrerEarnings).toNumber()
          )
        : 0;
      data.referrerCode = playerInfo.referrer
        ? new BigNumber(playerInfo.referrer).toNumber()
        : 0;
      data.level1RefCount = playerInfo.level1RefCount
        ? new BigNumber(playerInfo.level1RefCount).toNumber()
        : 0;
      data.level2RefCount = playerInfo.level2RefCount
        ? new BigNumber(playerInfo.level2RefCount).toNumber()
        : 0;
      data.level3RefCount = playerInfo.level3RefCount
        ? new BigNumber(playerInfo.level3RefCount).toNumber()
        : 0;
      data.planCount = playerInfo.planCount
        ? new BigNumber(playerInfo.planCount).toNumber()
        : 0;

      let currentDividends = 0;
      if (playerInfo.currentDividends) {
        if (playerInfo.currentDividends.length > 0) {
          for (let i = 0; i < playerInfo.currentDividends.length; i++) {
            currentDividends += sunToTrx(
              new BigNumber(playerInfo.currentDividends[i]).toNumber()
            );
          }
        }
      }

      let newDividends = 0;
      if (playerInfo.newDividends) {
        if (playerInfo.newDividends.length > 0) {
          for (let j = 0; j < playerInfo.newDividends.length; j++) {
            newDividends += sunToTrx(
              new BigNumber(playerInfo.newDividends[j]).toNumber()
            );
          }
        }
      }

      let hourlyProductions = 0;
      if (playerInfo.hourlyProductions) {
        if (playerInfo.hourlyProductions.length > 0) {
          for (let k = 0; k < playerInfo.hourlyProductions.length; k++) {
            hourlyProductions += sunToTrx(
              new BigNumber(playerInfo.hourlyProductions[k]).toNumber()
            );
          }
        }
      }

      data.currentDividends = toTruncateDown(
        new BigNumber(currentDividends),
        6
      );
      data.newDividends = toTruncateDown(new BigNumber(newDividends), 6);
      data.hourlyProductions = toTruncateDown(
        new BigNumber(hourlyProductions),
        6
      );
      data.lastWithdrawalDate = getDateTimeLongFromUnix(
        new BigNumber(playerInfo.lastWithdrawalDate).toNumber()
      );

      const hourlyProductionMin = await contract
        .getHourlyProduction(trxToSun(newDividends), 1, true)
        .call();
      const hourlyProductionMax = await contract
        .getHourlyProduction(trxToSun(newDividends), 60, true)
        .call();

      data.hourlyProductionMin = toTruncateDown(
        new BigNumber(sunToTrx(new BigNumber(hourlyProductionMin).toNumber())),
        6
      );
      data.hourlyProductionMax = toTruncateDown(
        new BigNumber(sunToTrx(new BigNumber(hourlyProductionMax).toNumber())),
        6
      );

      const playerInvestment = await contract
        .getInvestmentPlanByUID(playerUID)
        .call();

      let playerInvestments = [];
      let playerTotalInvestment = 0;
      let investmentCount = 0;
      if (playerInvestment) {
        investmentCount = playerInvestment.investments.length;
      }

      for (let k = 0; k < investmentCount; k++) {
        let inv = {};

        const investmentDate = getDateTimeLongFromUnix(
          new BigNumber(playerInvestment.investmentDates[k]).toNumber()
        );
        const investment = sunToTrx(
          new BigNumber(playerInvestment.investments[k]).toNumber()
        );
        const currentDividend = toTruncateDown(
          new BigNumber(
            sunToTrx(
              new BigNumber(playerInvestment.currentDividends[k]).toNumber()
            )
          ),
          6
        );

        const currentInterest = new BigNumber(
          playerInvestment.currentInterests[k]
        ).toNumber();

        const daysPassed = new BigNumber(
          playerInvestment.daysPassed[k]
        ).toNumber();
        const isBoost = playerInvestment.isBoosts[k];

        inv.investmentDate = investmentDate;
        inv.investment = investment;
        inv.currentDividend = currentDividend;
        inv.currentInterest = currentInterest;
        inv.isBoost = isBoost;
        inv.daysPassed = daysPassed;
        playerTotalInvestment = playerTotalInvestment + investment;

        playerInvestments.push(inv);
      }
      data.playerTotalInvestment = toTruncateDown(
        new BigNumber(playerTotalInvestment).toNumber(),
        6
      );

      data.playerInvestments = playerInvestments;

      return data;
    } catch (e) {
      console.error(e);
      return data;
    }
  },

  async currentDivsAddressBalance() {
    const balance = await window.tronWeb.trx.getBalance();
    return balance;
  },

  async invest(sendAmount, referrer) {
    if (tronWebState === TRON_WEB_STATE_NOT_INSTALLED) {
      store.dispatch(showSnackbar('message.requireInstall', SNACKBAR_ERROR));
      return false;
    } else if (tronWebState === TRON_WEB_STATE_LOCKED) {
      store.dispatch(showSnackbar('message.requireLogin', SNACKBAR_ERROR));
      return false;
    }
    const payAmount = trxToSun(sendAmount);

    const data = await contract.invest(referrer).send({
      feeLimit: gasLimit,
      callValue: payAmount,
      shouldPollResponse: false,
    });
    return data;
  },

  async boost() {
    if (tronWebState === TRON_WEB_STATE_NOT_INSTALLED) {
      store.dispatch(showSnackbar('message.requireInstall', SNACKBAR_ERROR));
      return false;
    } else if (tronWebState === TRON_WEB_STATE_LOCKED) {
      store.dispatch(showSnackbar('message.requireLogin', SNACKBAR_ERROR));
      return false;
    }
    const payAmount = 0;

    const data = await contract.boost().send({
      feeLimit: gasLimit,
      callValue: payAmount,
      shouldPollResponse: false,
    });
    return data;
  },

  async withdrawDivs() {
    if (tronWebState === TRON_WEB_STATE_NOT_INSTALLED) {
      store.dispatch(showSnackbar('message.requireInstall', SNACKBAR_ERROR));
      return false;
    } else if (tronWebState === TRON_WEB_STATE_LOCKED) {
      store.dispatch(showSnackbar('message.requireLogin', SNACKBAR_ERROR));
      return false;
    }
    const payAmount = 0;
    const data = await contract.withdraw().send({
      feeLimit: gasLimit,
      callValue: payAmount,
      shouldPollResponse: false,
    });
    return data;
  },

  async getLaunchDate() {
    const data = {};
    try {
      const info = await contract.getLaunchDate().call();
      data.launchDate = info.launchDate
        ? new BigNumber(info.launchDate).toNumber()
        : 0;
      data.isOpen = info.isOpen;
      return data;
    } catch (e) {
      console.error(e);
      // error when calling `getAllData`
      data.launchDate = null;
      data.isOpen = false;
      console.error(data);
      return data;
    }
  },
};
